.pokemon-frame {
	display: grid;
	grid-template-rows: 1fr 2fr;
	background: repeating-linear-gradient(0deg, $light-purple, $light-purple 5px, white 7px, white 10px);
	min-height: 25vh;
}

// Classes for Pokemon Attributes Component
//====================================================================================================
.pokemon-attributes{
	
	grid-column: 2 / span 2;
	background-color: #ccc;
}

.water{ background-color: $water-type; }
.normal{ background-color: $normal-type; }
.fire { background-color: $fire-type; }
.electric { background-color: $electric-type; }
.grass { background-color: $grass-type; }
.ice { background-color: $ice-type; }
.fighting { background-color: $fighting-type; }
.poison { background-color: $poison-type; }
.ground { background-color: $ground-type; }
.flying { background-color: $flying-type; }
.psychic { background-color: $psychic-type; }
.bug { background-color: $bug-type; }
.rock { background-color: $rock-type; }
.ghost { background-color: $ghost-type; }
.dragon { background-color: $dragon-type; }
.dark { background-color: $dark-type; }
.steel { background-color: $steel-type; }
.fairy { background-color: $fairy-type; }



// Classes for Text Container Component
//====================================================================================================
.text-container{
	grid-column: 1 / span 2;
	grid-row: 2;
	background-color: #aaa;
}
// Classes for Pokemon Stats Component
///====================================================================================================
.pokemon-stats{
	display: flex;
	flex-direction: column;
}

.stats{
	flex-grow: 1;
	display: flex;
	justify-content: space-between;
	align-content: center;
	text-transform: capitalize;
}

.hp{
	background-color: $color-hp;
}

.attack{
	background-color: $color-attack;
}

.defense{
	background-color: $color-defense;
}

.special-attack{
	background-color: $color-sp-attack;
}

.special-defense{
	background-color: $color-sp-defense;
}
.speed{
	background-color: $color-speed;
}